// - imports
import debounce from 'lodash.debounce';

import ApiService from './api-service';
import { moviesAll } from './movies-all';
import { movieSingle } from './movie-single';
import openModalWindow from './modal';
import './loading';


//refs 
const moviesList = document.querySelector('.list');
const modalWindow = document.querySelector('[data-modal]');
const searchInput = document.querySelector('#search-input');
const invalidFeedback = document.querySelector('.header-search__warning');


function addGenres(movies, genres) {
    //–∫–æ–∂–µ–Ω —Ñ—ñ–ª—å–º –æ–∫—Ä–µ–º–æ
    // {}- –¥–µ—Å—Ç—Ä—É–∫—Ç—É—Ä–∏–∑–∞—Ü—ñ—è - –¥—ñ—Å—Ç–∞—î–º–æ –≤–ª–∞—Å—Ç–∏–≤—ñ—Å—Ç—å –∑ –æ–±'—î–∫—Ç—É; –π ...otherProps - —ñ–Ω—à—ñ –≤–ª–∞—Å—Ç–∏–≤–æ—Å—Ç—ñ (–∞–ª–µ –±–µ–∑ genre_ids(–ø–æ–≤–µ—Ä—Ç–∞—î —á–∏—Å–ª–∞)) —Ç–æ–±—Ç–æ –º–∏ –∑–∞–±–∏—Ä–∞—î–º–æ –∂–∞–Ω—Ä–∏ –∑ –∫–æ–ª–µ–∫—Ü—ñ—ó movies
    return movies.map(({ genre_ids, ...otherProps }) => {
        // console.log(movies);
        //–≤–∏—Ç—è–≥—É—î–º–æ –∑ —á–∏—Å–µ–ª genre_ids - –Ω–∞–∑–≤–∏ –∂–∞–Ω—Ä—ñ–≤ —Å–ª–æ–≤–∞–º–∏ (–ø–µ—Ä–µ–∑–±–∏—Ä–∞—î–º–æ genre_ids)
        const genre_names = genre_ids.map((genreId) => {
            //–ø–µ—Ä–µ–±–∏—Ä–∞—î–º–æ —É—Å—ñ –∂–∞–Ω—Ä–∏ –∑–∞ –¥–æ–ø–æ–º–æ–≥–æ—é –º–µ—Ç–æ–¥—É find (—à—É–∫–∞—î–º–æ —Å–ø—ñ–≤–ø–∞–¥—ñ–Ω–Ω—è id —Ç–∞ –ø–æ–≤–µ—Ä—Ç–∞—î–º–æ –Ω–∞–∑–≤—É, —è–∫–∞ —ó–º –Ω–∞–ª–µ–∂–∏—Ç—å)
            return genres.find(({ id }) => genreId === id).name;
        });
        //–ø–æ–≤–µ—Ä—Ç–∞—î–º–æ –∂–∞–Ω—Ä–∏ —É –∫–æ–ª–µ–∫—Ü—ñ—é movies –∞–ª–µ –≤–∂–µ –Ω–µ —è–∫ —á–∏—Å–ª–∞, –∞ —è–∫ –Ω–∞–∑–≤–∏
        return {...otherProps, genre_names};
    });
};


function renderMovies(arr) {
    // –≤–∏–≤—ñ–¥ –≤ HTML
   moviesList.innerHTML = moviesAll(arr);
};

//–≤—ñ–¥–æ–±—Ä–∞–∂–µ–Ω–Ω—è —ñ–Ω—Ñ–æ—Ä–º–∞—Ü—ñ—ó –ø—Ä–æ –æ–±—Ä–∞–Ω–∏–π —Ñ—ñ–ª—å–º –≤ –º–æ–¥–∞–ª—å–Ω–æ–º—É –≤—ñ–∫–Ω—ñ.
async function renderMovieInfo(movieId) {
    // console.log('movieId: ', movieId);
    
    //–í–∏–∫–æ—Ä–∏—Å—Ç–∞–Ω–Ω—è –∫–ª—é—á–æ–≤–æ–≥–æ —Å–ª–æ–≤–∞ await –≤–∏–∫–æ–Ω—É—î—Ç—å—Å—è –≤ –∫–æ–Ω—Ç–µ–∫—Å—Ç—ñ –∞—Å–∏–Ω—Ö—Ä–æ–Ω–Ω–æ—ó —Ñ—É–Ω–∫—Ü—ñ—ó, —â–æ –¥–æ–∑–≤–æ–ª—è—î –ø—Ä–∏–∑—É–ø–∏–Ω–∏—Ç–∏ –≤–∏–∫–æ–Ω–∞–Ω–Ω—è —Ñ—É–Ω–∫—Ü—ñ—ó –¥–æ –æ—Ç—Ä–∏–º–∞–Ω–Ω—è —Ä–µ–∑—É–ª—å—Ç–∞—Ç—É –≤—ñ–¥ api.getMovieById(movieId).
    const result = await api.getMovieById(movieId);
    //–¥–∞–Ω—ñ –ø–µ—Ä–µ–¥–∞—é—Ç—å—Å—è —Ñ—É–Ω–∫—Ü—ñ—ó movieSingle, —è–∫–∞ –≤—ñ–¥–ø–æ–≤—ñ–¥–∞—î –∑–∞ —Ñ–æ—Ä–º–∞—Ç—É–≤–∞–Ω–Ω—è —ñ–Ω—Ñ–æ—Ä–º–∞—Ü—ñ—ó –ø—Ä–æ —Ñ—ñ–ª—å–º —É –≤—ñ–¥–ø–æ–≤—ñ–¥–Ω–æ–º—É –≤–∏–≥–ª—è–¥—ñ. –†–µ–∑—É–ª—å—Ç–∞—Ç –≤–∏–∫–æ–Ω–∞–Ω–Ω—è —Ñ—É–Ω–∫—Ü—ñ—ó movieSingle –ø–æ—Ç—ñ–º –¥–æ–¥–∞—î—Ç—å—Å—è –¥–æ –≤–º—ñ—Å—Ç—É –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –≤—ñ–∫–Ω–∞ –∑ –¥–æ–ø–æ–º–æ–≥–æ—é –≤–ª–∞—Å—Ç–∏–≤–æ—Å—Ç—ñ innerHTML.
    modalWindow.innerHTML = movieSingle(result);
};

//–≤—ñ–¥–ø–æ–≤—ñ–¥–∞—î –∑–∞ –æ–±—Ä–æ–±–∫—É –∫–ª—ñ–∫—É –Ω–∞ –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—ñ —Ñ—ñ–ª—å–º—É, —è–∫–µ –º—ñ—Å—Ç–∏—Ç—å—Å—è –≤ –µ–ª–µ–º–µ–Ω—Ç—ñ –∑ –∫–ª–∞—Å–æ–º list-item__img. –§—É–Ω–∫—Ü—ñ—è onMovieClick –≤–∏–∫–ª–∏–∫–∞—î—Ç—å—Å—è, –∫–æ–ª–∏ —Å—Ç–∞–ª–∞—Å—è –ø–æ–¥—ñ—è click –Ω–∞ –µ–ª–µ–º–µ–Ω—Ç—ñ –∑ —Å–ø–∏—Å–∫—É —Ñ—ñ–ª—å–º—ñ–≤.
function onMovieClick(e) {
    if (!e.target.classList.contains('list-item__img')) return;
    //–∫–æ–ª–∏ –∫–æ—Ä–∏—Å—Ç—É–≤–∞—á –∫–ª—ñ–∫–∞—î –Ω–∞ –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—ñ —Ñ—ñ–ª—å–º—É, –≤–∏–∫–ª–∏–∫–∞—é—Ç—å—Å—è –¥–≤—ñ —Ñ—É–Ω–∫—Ü—ñ—ó, —è–∫—ñ –≤—ñ–¥–ø–æ–≤—ñ–¥–∞—é—Ç—å –∑–∞ –≤—ñ–¥–æ–±—Ä–∞–∂–µ–Ω–Ω—è –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –≤—ñ–∫–Ω–∞ —Ç–∞ –∑–∞–ø–æ–≤–Ω–µ–Ω–Ω—è —ñ–Ω—Ñ–æ—Ä–º–∞—Ü—ñ—î—é –ø—Ä–æ —Ñ—ñ–ª—å–º.
    openModalWindow();
    renderMovieInfo(e.target.dataset.id);
};

function onSearchInput(e) {
    //–≤—ñ–¥–ø—Ä–∞–≤–ª—è—î–º–æ –∑–∞–ø–∏—Ç –Ω–∞ api, –∫–æ–ª–∏ –Ω–∞–ø–∏—Å–∞–Ω–æ –≤—ñ–¥ 2 —Å–∏–º–≤–æ–ª—ñ–≤ —É –ø–æ—à—É–∫—É
    //—è–∫—â–æ –¥–æ–≤–∂–∏–Ω–∞ –≤ —ñ–Ω–ø—É—Ç—ñ –±—ñ–ª—å—à–µ 2 —Å–∏–º–≤–æ–ª—ñ–≤
    if (e.target.value.length > 2) {
        //—Ö–æ–≤–∞—î–º–æ –µ–ª–µ–º–µ–Ω—Ç –∑ —Ñ—ñ–¥–±–µ–∫–æ–º, —è–∫–∏–π –≤—ñ–¥–æ–±—Ä–∞–∂–∞—î –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è –ø—Ä–æ –ø–æ–º–∏–ª–∫—É.
        invalidFeedback.classList.add('visually-hidden');
        //–í–∏–∫–ª–∏–∫–∞—î—Ç—å—Å—è –º–µ—Ç–æ–¥ Promise.all(), —è–∫–∏–π –ø—Ä–∏–π–º–∞—î –¥–≤–∞ –ø–∞—Ä–∞–º–µ—Ç—Ä–∏ - –æ–¥–∏–Ω –¥–ª—è –æ—Ç—Ä–∏–º–∞–Ω–Ω—è —Ñ—ñ–ª—å–º—ñ–≤ –∑–∞ –∫–ª—é—á–æ–≤–∏–º —Å–ª–æ–≤–æ–º, –≤–≤–µ–¥–µ–Ω–∏–º –∫–æ—Ä–∏—Å—Ç—É–≤–∞—á–µ–º, —ñ –¥—Ä—É–≥–∏–π –¥–ª—è –æ—Ç—Ä–∏–º–∞–Ω–Ω—è –∂–∞–Ω—Ä—ñ–≤.
        Promise.all([api.getMoviesByKeyWords(e.target.value), api.getGenres()])
            //–ø—ñ—Å–ª—è –≤–∏–∫–æ–Ω–∞–Ω–Ω—è –æ–±—ñ—Ü—è–Ω–æ–∫ –≤–∏–∫–æ–Ω—É—î—Ç—å—Å—è –º–µ—Ç–æ–¥ then(), —è–∫–∏–π –æ—Ç—Ä–∏–º—É—î –º–∞—Å–∏–≤ –∑ –¥–≤–æ—Ö –æ–±'—î–∫—Ç—ñ–≤, —è–∫—ñ –º—ñ—Å—Ç—è—Ç—å –≤—ñ–¥–ø–æ–≤—ñ–¥–Ω–æ –º–∞—Å–∏–≤ –∑ —Ñ—ñ–ª—å–º–∞–º–∏ —Ç–∞ –º–∞—Å–∏–≤ –∑ –∂–∞–Ω—Ä–∞–º–∏.
            .then(([{ results: movies }, { genres }]) => {
                //–î–∞–ª—ñ –ø–µ—Ä–µ–≤—ñ—Ä—è—î—Ç—å—Å—è, —á–∏ —î –≤—ñ–¥–ø–æ–≤—ñ–¥—å –≤—ñ–¥ —Å–µ—Ä–≤–µ—Ä–∞ - –º–∞—Å–∏–≤ —Ñ—ñ–ª—å–º—ñ–≤ –Ω–µ –ø–æ–≤–∏–Ω–µ–Ω –±—É—Ç–∏ –ø–æ—Ä–æ–∂–Ω—ñ–º. –Ø–∫—â–æ –≤—ñ–¥–ø–æ–≤—ñ–¥—å —î, —Ç–æ –≤–∏–∫–ª–∏–∫–∞—î—Ç—å—Å—è —Ñ—É–Ω–∫—Ü—ñ—è addGenres, —è–∫–∞ –¥–æ–¥–∞—î –¥–æ –∫–æ–∂–Ω–æ–≥–æ —Ñ—ñ–ª—å–º—É –≤–ª–∞—Å—Ç–∏–≤—ñ—Å—Ç—å genres –∑—ñ —Å–ø–∏—Å–∫–æ–º –∂–∞–Ω—Ä—ñ–≤, —è–∫—ñ –≤—ñ–¥–ø–æ–≤—ñ–¥–∞—é—Ç—å —Ü—å–æ–º—É —Ñ—ñ–ª—å–º—É.
                if (movies.length > 0) {
                    return addGenres(movies, genres);   
                };
                //–Ø–∫—â–æ –º–∞—Å–∏–≤ —Ñ—ñ–ª—å–º—ñ–≤ –ø–æ—Ä–æ–∂–Ω—ñ–π, —Ç–æ –≤–∏–∫–ª–∏–∫–∞—î—Ç—å—Å—è –ø–æ–º–∏–ª–∫–∞ –∑ –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è–º 'Empty results'.
                throw new Error('Empty results');
            })
            //–ü—ñ—Å–ª—è –≤–∏–∫–æ–Ω–∞–Ω–Ω—è —Ñ—É–Ω–∫—Ü—ñ—ó addGenres, –≤–∏–∫–æ–Ω—É—î—Ç—å—Å—è –º–µ—Ç–æ–¥ then(), —è–∫–∏–π –æ—Ç—Ä–∏–º—É—î –º–æ–¥–∏—Ñ—ñ–∫–æ–≤–∞–Ω—É –∫–æ–ª–µ–∫—Ü—ñ—é —Ñ—ñ–ª—å–º—ñ–≤ —Ç–∞ –≤–∏–∫–ª–∏–∫–∞—î —Ñ—É–Ω–∫—Ü—ñ—é renderMovies –¥–ª—è —ó—Ö –≤—ñ–¥–æ–±—Ä–∞–∂–µ–Ω–Ω—è.
            .then((result) => renderMovies(result))
            //–Ø–∫—â–æ –≤–∏–∫–æ–Ω–∞–Ω–Ω—è –±—É–¥—å-—è–∫–æ—ó –∑ "–æ–±—ñ—Ü—è–Ω–æ–∫" –∑–∞–≤–µ—Ä—à–∏—Ç—å—Å—è –∑ –ø–æ–º–∏–ª–∫–æ—é, –≤–∏–∫–æ–Ω—É—î—Ç—å—Å—è –º–µ—Ç–æ–¥ catch(), —è–∫–∏–π –æ—Ç—Ä–∏–º—É—î –æ–±'—î–∫—Ç –ø–æ–º–∏–ª–∫–∏ —Ç–∞ –≤—ñ–¥–æ–±—Ä–∞–∂–∞—î –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è –ø—Ä–æ –ø–æ–º–∏–ª–∫—É. –£ –¥–∞–Ω–æ–º—É –≤–∏–ø–∞–¥–∫—É –≤–∏–≤–æ–¥–∏—Ç—å—Å—è –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è –ø—Ä–æ –ø–æ–º–∏–ª–∫—É –≤ –∫–æ–Ω—Å–æ–ª—å, –∞ –µ–ª–µ–º–µ–Ω—Ç –∑ —Ñ—ñ–¥–±–µ–∫–æ–º –≤—ñ–¥–æ–±—Ä–∞–∂–∞—î—Ç—å—Å—è –ø—ñ–¥ —ñ–Ω–ø—É—Ç–æ–º.
            .catch((err) => {
                console.log('render: catch error: ', err.message);
                invalidFeedback.classList.remove('visually-hidden');
            })
    }; 
};

//init
//–∑–º—ñ–Ω–Ω–∞ api –º–æ–∂–µ –≤–∏–∫–æ—Ä–∏—Å—Ç–æ–≤—É–≤–∞—Ç–∏—Å—å —É –∫–æ–¥—ñ –¥–ª—è –≤–∏–∫–ª–∏–∫—É –º–µ—Ç–æ–¥—ñ–≤, —è–∫—ñ –Ω–∞–¥–∞—é—Ç—å –¥–æ—Å—Ç—É–ø –¥–æ —Ä–µ—Å—É—Ä—Å—ñ–≤ API, —Ç–∞–∫–∏—Ö —è–∫ —Ñ—ñ–ª—å–º–∏ –∞–±–æ –∂–∞–Ω—Ä–∏.
const api = new ApiService();


Promise.all([api.getPopular(), api.getGenres()])
    .then(([{ results: movies }, { genres }]) => addGenres(movies, genres))
    .then((result) => renderMovies(result));

moviesList.addEventListener('click', onMovieClick);
searchInput.addEventListener('input', debounce(onSearchInput, 1200));
